<template>
  <view class="orders-container">
    <!-- È°∂ÈÉ®ÂØºËà™Ê†è -->
    <view class="nav-bar">
      <view class="nav-title">ÊàëÁöÑËÆ¢Âçï</view>
    </view>

    <!-- ËÆ¢ÂçïÁä∂ÊÄÅÊ†áÁ≠æ -->
    <view class="tabs-wrapper">
      <view class="tabs-list">
        <view
          class="tab-item"
          :class="{ active: activeTab === item.value }"
          v-for="item in statusTabs"
          :key="item.value"
          @click="switchTab(item.value)"
        >
          <text class="tab-text">{{ item.label }}</text>
          <view class="tab-badge" v-if="item.count > 0">{{ item.count }}</view>
        </view>
      </view>
    </view>

    <!-- ËÆ¢ÂçïÂàóË°®ÂÜÖÂÆπ -->
    <scroll-view
      class="orders-scroll"
      scroll-y
      @scrolltolower="loadMore"
      refresher-enabled
      :refresher-triggered="refreshing"
      @refresherrefresh="onRefresh"
    >
      <!-- ËÆ¢ÂçïÂàóË°® -->
      <view class="order-list">
        <view
          class="order-card"
          v-for="order in orderList"
          :key="order.id"
          @click="goToDetail(order.id)"
        >
          <!-- ËÆ¢ÂçïÂ§¥ÈÉ® -->
          <view class="order-header">
            <view class="order-no">ËÆ¢ÂçïÂè∑Ôºö{{ order.orderNo }}</view>
            <view class="order-status" :class="'status-' + order.status">
              {{ order.statusDesc }}
            </view>
          </view>

          <!-- ËÆ¢ÂçïÂÜÖÂÆπ -->
          <view class="order-content">
            <view class="service-type">
              <text class="type-icon">{{ getTypeIcon(order.type) }}</text>
              <text class="type-name">{{ getTypeName(order.type) }}</text>
            </view>

            <view class="goods-info">
              <text class="goods-label">Áâ©ÂìÅÔºö</text>
              <text class="goods-text">{{ order.goodsDesc }}</text>
            </view>

            <view class="address-info">
              <view class="address-item">
                <text class="address-label">Âèñ</text>
                <text class="address-text">{{ order.pickupAddr }}</text>
              </view>
              <view class="address-item">
                <text class="address-label">ÈÄÅ</text>
                <text class="address-text">{{ order.deliveryAddr }}</text>
              </view>
            </view>

            <view class="order-footer">
              <view class="order-time">
                <text>{{ formatTime(order.createTime) }}</text>
              </view>
              <view class="order-amount">
                <text class="amount-label">ËÆ¢ÂçïÈáëÈ¢ùÔºö</text>
                <text class="amount-value">¬•{{ order.totalFee }}</text>
              </view>
            </view>
          </view>

          <!-- Êìç‰ΩúÊåâÈíÆ -->
          <view class="order-actions">
            <!-- ÂæÖÊîØ‰ªò -->
            <template v-if="order.status === 0">
              <button class="btn-action btn-cancel" @click.stop="cancelOrder(order.id)">ÂèñÊ∂àËÆ¢Âçï</button>
              <button class="btn-action btn-primary" @click.stop="goToPay(order.id, order.totalFee)">ÂéªÊîØ‰ªò</button>
            </template>

            <!-- ÂæÖÊé•Âçï -->
            <template v-if="order.status === 1">
              <button class="btn-action btn-cancel" @click.stop="cancelOrder(order.id)">ÂèñÊ∂àËÆ¢Âçï</button>
            </template>

            <!-- ÈÖçÈÄÅ‰∏≠ -->
            <template v-if="order.status === 2">
              <button class="btn-action btn-primary" @click.stop="contactRider(order)">ËÅîÁ≥ªÈ™ëÊâã</button>
            </template>

            <!-- Â∑≤ÂÆåÊàê - Êú™ËØÑ‰ª∑ -->
            <template v-if="order.status === 3 && (order.rating === null || order.rating === undefined)">
              <button class="btn-action btn-primary" @click.stop="evaluateOrder(order.id)">ÂéªËØÑ‰ª∑</button>
            </template>

            <!-- Â∑≤ÂÆåÊàê - Â∑≤ËØÑ‰ª∑ -->
            <template v-if="order.status === 3 && (order.rating !== null && order.rating !== undefined)">
              <button class="btn-action btn-default" @click.stop="viewEvaluation(order.id)">Êü•ÁúãËØÑ‰ª∑</button>
            </template>

            <!-- Â∑≤ÂèñÊ∂à -->
            <template v-if="order.status === 4">
              <button class="btn-action btn-default" @click.stop="deleteOrder(order.id)">Âà†Èô§ËÆ¢Âçï</button>
              <button class="btn-action btn-primary" @click.stop="reorder(order.id)">ÂÜçÊù•‰∏ÄÂçï</button>
            </template>
          </view>
        </view>
      </view>

      <!-- Âä†ËΩΩÊõ¥Â§ö -->
      <view class="load-more" v-if="orderList.length > 0">
        <text v-if="loading">Âä†ËΩΩ‰∏≠...</text>
        <text v-else-if="hasMore">‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö</text>
        <text v-else>Ê≤°ÊúâÊõ¥Â§ö‰∫Ü</text>
      </view>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <view class="empty-state" v-if="orderList.length === 0 && !loading">
        <text class="empty-icon">üì¶</text>
        <text class="empty-title">ÊöÇÊó†ËÆ¢Âçï</text>
        <text class="empty-desc">Âø´ÂéªÂèëÂ∏ÉÁ¨¨‰∏Ä‰∏™ËÆ¢ÂçïÂêß~</text>
      </view>
    </scroll-view>
  </view>
</template>

<script>
import { getOrderList, cancelOrder } from '@/api/order.js'

export default {
  data() {
    return {
      // Áä∂ÊÄÅÊ†áÁ≠æÔºà‰∏éÂêéÁ´ØOrderStatusEnum‰øùÊåÅ‰∏ÄËá¥Ôºâ
      statusTabs: [
        { label: 'ÂÖ®ÈÉ®', value: null, count: 0 },
        { label: 'ÂæÖÊîØ‰ªò', value: 0, count: 0 },
        { label: 'ÂæÖÊé•Âçï', value: 1, count: 0 },
        { label: 'ÈÖçÈÄÅ‰∏≠', value: 2, count: 0 },
        { label: 'Â∑≤ÂÆåÊàê', value: 3, count: 0 },
        { label: 'Â∑≤ÂèñÊ∂à', value: 4, count: 0 }
      ],

      activeTab: null, // ÂΩìÂâçÊøÄÊ¥ªÁöÑÊ†áÁ≠æ
      orderList: [], // ËÆ¢ÂçïÂàóË°®
      loading: false, // Âä†ËΩΩÁä∂ÊÄÅ
      refreshing: false, // Âà∑Êñ∞Áä∂ÊÄÅ
      hasMore: true, // ÊòØÂê¶ÊúâÊõ¥Â§öÊï∞ÊçÆ

      // ÂàÜÈ°µÂèÇÊï∞
      page: 1,
      size: 10
    }
  },

  onLoad(options) {
    // Â¶ÇÊûú‰ªéÂÖ∂‰ªñÈ°µÈù¢Ë∑≥ËΩ¨ËøáÊù•ÔºåÂ∏¶ÊúâÁä∂ÊÄÅÂèÇÊï∞
    if (options.status !== undefined) {
      this.activeTab = Number(options.status)
    }

    // Âä†ËΩΩËÆ¢ÂçïÂàóË°®
    this.loadOrderList()
  },

  onShow() {
    // ‰ªéËÆ¢ÂçïËØ¶ÊÉÖÊàñÊîØ‰ªòÈ°µËøîÂõûÊó∂Âà∑Êñ∞ÂàóË°®
    this.refreshList()
  },

  methods: {
    /**
     * ÂàáÊç¢Ê†áÁ≠æ
     */
    switchTab(status) {
      this.activeTab = status
      this.page = 1
      this.orderList = []
      this.hasMore = true
      this.loadOrderList()
    },

    /**
     * Âä†ËΩΩËÆ¢ÂçïÂàóË°®
     */
    async loadOrderList() {
      if (this.loading) return

      try {
        this.loading = true
        console.log('üì• ÂºÄÂßãÂä†ËΩΩËÆ¢ÂçïÂàóË°®')
        console.log('   - È°µÁ†Å:', this.page)
        console.log('   - ÊØèÈ°µÊï∞Èáè:', this.size)
        console.log('   - Áä∂ÊÄÅÁ≠õÈÄâ:', this.activeTab)

        const params = {
          page: this.page,
          size: this.size
        }

        // Â¶ÇÊûúÈÄâÊã©‰∫ÜÁâπÂÆöÁä∂ÊÄÅÔºåÊ∑ªÂä†Áä∂ÊÄÅÂèÇÊï∞
        if (this.activeTab !== null) {
          params.status = this.activeTab
        }

        console.log('   - ËØ∑Ê±ÇÂèÇÊï∞:', JSON.stringify(params))

        const res = await getOrderList(params)

        console.log('üì• ËÆ¢ÂçïÂàóË°®APIÂìçÂ∫î:')
        console.log('   - ÂìçÂ∫îÁ†Å:', res.code)
        console.log('   - ÂìçÂ∫îÊ∂àÊÅØ:', res.message)
        console.log('   - ÂìçÂ∫îÊï∞ÊçÆ:', JSON.stringify(res.data, null, 2))

        if (res.code === 200 && res.data) {
          const newList = res.data.records || []

          console.log('üìä Ëß£ÊûêËÆ¢ÂçïÂàóË°®:')
          console.log('   - ËÆ∞ÂΩïÊÄªÊï∞:', res.data.total)
          console.log('   - ÂΩìÂâçÈ°µËÆ∞ÂΩïÊï∞:', newList.length)
          console.log('   - ËÆ∞ÂΩïËØ¶ÊÉÖ:', JSON.stringify(newList, null, 2))

          if (this.page === 1) {
            this.orderList = newList
          } else {
            this.orderList = [...this.orderList, ...newList]
          }

          // Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
          this.hasMore = newList.length >= this.size

          console.log('‚úÖ ËÆ¢ÂçïÂàóË°®Âä†ËΩΩÊàêÂäüÔºåÂΩìÂâçÂàóË°®ÂÖ±', this.orderList.length, 'Êù°')
        } else {
          console.error('‚ùå APIËøîÂõûÈîôËØØ:', res.code, res.message)
          uni.showToast({
            title: res.message || 'Âä†ËΩΩÂ§±Ë¥•',
            icon: 'none'
          })
        }
      } catch (error) {
        console.error('‚ùå Âä†ËΩΩËÆ¢ÂçïÂàóË°®ÂºÇÂ∏∏:', error)
        console.error('   - ÈîôËØØ‰ø°ÊÅØ:', error.message)
        console.error('   - ÈîôËØØÂ†ÜÊ†à:', error.stack)
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
          icon: 'none'
        })
      } finally {
        this.loading = false
        this.refreshing = false
        console.log('üîÑ Âä†ËΩΩÁä∂ÊÄÅÂ∑≤ÈáçÁΩÆ')
      }
    },

    /**
     * ‰∏ãÊãâÂà∑Êñ∞
     */
    async onRefresh() {
      this.refreshing = true
      this.page = 1
      await this.loadOrderList()
    },

    /**
     * Âà∑Êñ∞ÂàóË°®
     */
    refreshList() {
      this.page = 1
      this.loadOrderList()
    },

    /**
     * Âä†ËΩΩÊõ¥Â§ö
     */
    loadMore() {
      if (!this.hasMore || this.loading) return

      this.page++
      this.loadOrderList()
    },

    /**
     * Ë∑≥ËΩ¨Âà∞ËÆ¢ÂçïËØ¶ÊÉÖ
     */
    goToDetail(orderId) {
      uni.navigateTo({
        url: `/pages/order/detail?id=${orderId}`
      })
    },

    /**
     * Ë∑≥ËΩ¨Âà∞ÊîØ‰ªòÈ°µÈù¢
     */
    goToPay(orderId, totalFee) {
      uni.navigateTo({
        url: `/pages/order/payment?orderId=${orderId}&totalAmount=${totalFee}`
      })
    },

    /**
     * ÂèñÊ∂àËÆ¢Âçï
     */
    cancelOrder(orderId) {
      uni.showModal({
        title: 'ÂèñÊ∂àËÆ¢Âçï',
        content: 'Á°ÆÂÆöË¶ÅÂèñÊ∂àËøô‰∏™ËÆ¢ÂçïÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            try {
              uni.showLoading({ title: 'ÂèñÊ∂à‰∏≠...' })

              const cancelRes = await cancelOrder({
                orderId: orderId,
                cancelReason: 'Áî®Êà∑‰∏ªÂä®ÂèñÊ∂à'
              })

              uni.hideLoading()

              if (cancelRes.code === 200) {
                uni.showToast({
                  title: 'ËÆ¢ÂçïÂ∑≤ÂèñÊ∂à',
                  icon: 'success'
                })
                // Âà∑Êñ∞ÂàóË°®
                this.refreshList()
              } else {
                uni.showToast({
                  title: cancelRes.message || 'ÂèñÊ∂àÂ§±Ë¥•',
                  icon: 'none'
                })
              }
            } catch (error) {
              uni.hideLoading()
              console.error('‚ùå ÂèñÊ∂àËÆ¢ÂçïÂ§±Ë¥•:', error)
              uni.showToast({
                title: 'ÂèñÊ∂àÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
                icon: 'none'
              })
            }
          }
        }
      })
    },

    /**
     * Âà†Èô§ËÆ¢Âçï
     */
    deleteOrder(orderId) {
      uni.showModal({
        title: 'Âà†Èô§ËÆ¢Âçï',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™ËÆ¢ÂçïÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            // TODO: Ë∞ÉÁî®Âà†Èô§ËÆ¢ÂçïÊé•Âè£
            uni.showToast({
              title: 'ÂäüËÉΩÂºÄÂèë‰∏≠',
              icon: 'none'
            })
          }
        }
      })
    },

    /**
     * ËÅîÁ≥ªÈ™ëÊâã
     */
    contactRider(order) {
      // TODO: ÂÆûÁé∞ËÅîÁ≥ªÈ™ëÊâãÂäüËÉΩ
      uni.showToast({
        title: 'ÂäüËÉΩÂºÄÂèë‰∏≠',
        icon: 'none'
      })
    },

    /**
     * ËØÑ‰ª∑ËÆ¢Âçï
     */
    evaluateOrder(orderId) {
      uni.navigateTo({
        url: `/pages/evaluation/create?orderId=${orderId}`
      })
    },

    /**
     * Êü•ÁúãËØÑ‰ª∑
     */
    viewEvaluation(orderId) {
      uni.navigateTo({
        url: `/pages/evaluation/detail?orderId=${orderId}`
      })
    },

    /**
     * ÂÜçÊù•‰∏ÄÂçï
     */
    reorder(orderId) {
      uni.navigateTo({
        url: `/pages/order/create?orderId=${orderId}`
      })
    },

    /**
     * Ëé∑ÂèñÊúçÂä°Á±ªÂûãÂõæÊ†á
     */
    getTypeIcon(type) {
      const icons = {
        1: 'üõí',
        2: 'üì¶',
        3: 'üîë',
        4: 'üåü'
      }
      return icons[type] || 'üì¶'
    },

    /**
     * Ëé∑ÂèñÊúçÂä°Á±ªÂûãÂêçÁß∞
     */
    getTypeName(type) {
      const names = {
        1: 'Â∏ÆÊàë‰π∞',
        2: 'Â∏ÆÊàëÈÄÅ',
        3: 'Â∏ÆÊàëÂèñ',
        4: 'ÂÖ®ËÉΩ'
      }
      return names[type] || 'Êú™Áü•'
    },

    /**
     * Ê†ºÂºèÂåñÊó∂Èó¥
     */
    formatTime(time) {
      if (!time) return ''

      const date = new Date(time)
      const now = new Date()
      const diff = now.getTime() - date.getTime()

      // Â∞è‰∫é1ÂàÜÈíü
      if (diff < 60000) {
        return 'ÂàöÂàö'
      }

      // Â∞è‰∫é1Â∞èÊó∂
      if (diff < 3600000) {
        return Math.floor(diff / 60000) + 'ÂàÜÈíüÂâç'
      }

      // Â∞è‰∫é1Â§©
      if (diff < 86400000) {
        return Math.floor(diff / 3600000) + 'Â∞èÊó∂Ââç'
      }

      // Â§ß‰∫é1Â§©ÔºåÊòæÁ§∫ÂÖ∑‰ΩìÊó•Êúü
      const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0')
      const day = String(date.getDate()).padStart(2, '0')
      const hour = String(date.getHours()).padStart(2, '0')
      const minute = String(date.getMinutes()).padStart(2, '0')

      return `${year}-${month}-${day} ${hour}:${minute}`
    }
  }
}
</script>

<style lang="scss" scoped>
.orders-container {
  min-height: 100vh;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
}

/* ÂØºËà™Ê†è */
.nav-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 44px;
  background-color: #fff;
  border-bottom: 1px solid #eee;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.nav-title {
  font-size: 17px;
  font-weight: 600;
  color: #333;
}

/* Ê†áÁ≠æÊ†è */
.tabs-wrapper {
  background-color: #fff;
  position: fixed;
  top: 44px;
  left: 0;
  right: 0;
  z-index: 99;
  border-bottom: 1px solid #eee;
}

.tabs-list {
  display: flex;
  padding: 0 15px;
}

.tab-item {
