  background-color: #ff4d4f;
  color: #fff;
  font-size: 10px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ÊªöÂä®Âå∫Âüü */
.orders-scroll {
  flex: 1;
  margin-top: 98px;
  height: calc(100vh - 98px);
}

/* ËÆ¢ÂçïÂàóË°® */
.order-list {
  padding: 10px 15px;
}

.order-card {
  background-color: #fff;
  border-radius: 8px;
  margin-bottom: 10px;
  overflow: hidden;
