}

/* ËÆ¢ÂçïÂ§¥ÈÉ® */
.order-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 15px;
  border-bottom: 1px solid #f5f5f5;
}

.order-no {
  font-size: 13px;
  color: #999;
}

.order-status {
  font-size: 13px;
  font-weight: 600;
}

.order-status.status-0 {
  color: #ff9800;
}

.order-status.status-1 {
  color: #2196f3;
}

.order-status.status-2 {
  color: #9c27b0;
}

.order-status.status-3 {
  color: #4caf50;
}

.order-status.status-4 {
  color: #ff5722;
}

.order-status.status-5 {
  color: #999;
}

.order-status.status-6 {
  color: #999;
}

/* ËÆ¢ÂçïÂÜÖÂÆπ */
.order-content {
  padding: 15px;
}

.service-type {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.type-icon {
  font-size: 20px;
  margin-right: 8px;
}

.type-name {
  font-size: 15px;
  font-weight: 600;
  color: #333;
}

.goods-info {
  margin-bottom: 12px;
  display: flex;
  align-items: flex-start;
}

.goods-label {
  font-size: 13px;
  color: #999;
  flex-shrink: 0;
}

.goods-text {
  font-size: 14px;
  color: #333;
  line-height: 1.5;
  flex: 1;
}

.address-info {
  margin-bottom: 12px;
}

.address-item {
