  align-items: center;
  justify-content: center;
  margin-right: 8px;
  flex-shrink: 0;
}

.address-text {
  font-size: 13px;
  color: #666;
  line-height: 1.5;
  flex: 1;
}

.order-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 12px;
  border-top: 1px solid #f5f5f5;
}

.order-time {
  font-size: 12px;
  color: #999;
}

.order-amount {
  display: flex;
  align-items: baseline;
}

.amount-label {
  font-size: 12px;
